@import url(https://unpkg.com/@tailwindcss/typography@0.2.x/dist/typography.min.css);
@import url(https://unpkg.com/tailwindcss@^2/dist/tailwind.min.css);
header {
    margin-bottom: 20px;
}

* {
    font-family:Arial, Helvetica, sans-serif;
}

.main-wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}


main {
    margin-bottom: auto;
}

footer {
    background-color: rgb(240, 240, 240);
}

.footer-form input:invalid {
    border-width: 1px;
    border-style: solid;
    border-color: rgb(218, 47, 47);
}

.fa-heart-o {
    cursor: pointer;
}
.fa-heart {
    cursor: pointer;
    color: red;
}

.splash {
    width: 100%;
    height: 100%;
    margin-bottom: 20px
}

#dropdownId {
    cursor: pointer;
}

.author {
    border-radius: 50%;
}

.slider {
    position: relative;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .image {
    width: 1000px;
    height: 600px;
    border-radius: 10px;
  }
  
  .right-arrow {
    position: absolute;
    top: 50%;
    right: 32px;
    font-size: 3rem;
    color: rgb(236, 230, 230);
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .left-arrow {
    position: absolute;
    top: 50%;
    left: 32px;
    font-size: 3rem;
    color: rgb(236, 230, 230);
    z-index: 10;
    cursor: pointer;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .slide {
    opacity: 0;
    transition-duration: 1s ease;
  }
  
  .slide.active {
    opacity: 1;
    transition-duration: 1s;
    -webkit-transform: scale(1.08);
            transform: scale(1.08);
  }
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgb(225, 236, 236);
    display: flex;
    justify-content: center;
    align-items: center;
}

.popup-inner {
    position: relative;
    padding: 35px;
    width: 100%;
    background-color: rgb(204, 218, 218);
}

.popup-inner .close-btn {
    position: absolute;
    top: 16px;
    right: 16px;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

